<script>
import TabAd from '../public/tabad'
import GoodItemMid from '../public/good-item-mid'

export default {
    name:'hot-presale',
    components:{
        TabAd,
        GoodItemMid
    },
    data(){
        return {
            rules:[
                '1',
                '2400-870-3700',
                '3',
                '4'
            ]
        }
    }
}
</script>
<template>
    <div 
    class="hot-presale"
    >
        <tab-ad :rules="rules"></tab-ad>
        <div class="hot-presale-list">
            <template
            v-for="(i,k) in 10"
            >
                <good-item-mid :key="k"></good-item-mid>
            </template>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
.hot-presale 
    position relative 
    &-list 
        display flex 
        flex-wrap wrap 
        justify-content space-between
        padding .25rem
        background-color #f6f6f6
</style>



